namespace $ {
	export class $scale_form_exit extends $mol_list {
		
		/**
		 * ```tree
		 * default_values * act \Выберите гос. номер
		 * ```
		 */
		default_values() {
			return {
				act: "Выберите гос. номер"
			}
		}
		
		/**
		 * ```tree
		 * api $scale_api
		 * ```
		 */
		@ $mol_mem
		api() {
			const obj = new this.$.$scale_api()
			
			return obj
		}
		
		/**
		 * ```tree
		 * rows / <= Form
		 * ```
		 */
		rows() {
			return [
				this.Form()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * weight
		 * ```
		 */
		weight() {
			return this.Centrifuge().weight_channel()
		}
		
		/**
		 * ```tree
		 * Centrifuge $scale_centrifuge weight_channel => weight
		 * ```
		 */
		@ $mol_mem
		Centrifuge() {
			const obj = new this.$.$scale_centrifuge()
			
			return obj
		}
		
		/**
		 * ```tree
		 * weight_bid \
		 * ```
		 */
		weight_bid() {
			return ""
		}
		
		/**
		 * ```tree
		 * weight_formatted \0
		 * ```
		 */
		weight_formatted() {
			return "0"
		}
		
		/**
		 * ```tree
		 * Weight_control $mol_paragraph title <= weight_formatted
		 * ```
		 */
		@ $mol_mem
		Weight_control() {
			const obj = new this.$.$mol_paragraph()
			
			obj.title = () => this.weight_formatted()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Weight_field $mol_form_field
		 * 	name \Текущий вес
		 * 	bid <= weight_bid
		 * 	Content <= Weight_control
		 * ```
		 */
		@ $mol_mem
		Weight_field() {
			const obj = new this.$.$mol_form_field()
			
			obj.name = () => "Текущий вес"
			obj.bid = () => this.weight_bid()
			obj.Content = () => this.Weight_control()
			
			return obj
		}
		
		/**
		 * ```tree
		 * act_bid \
		 * ```
		 */
		act_bid() {
			return ""
		}
		
		/**
		 * ```tree
		 * act?val \Выберите гос. номер
		 * ```
		 */
		@ $mol_mem
		act(val?: any) {
			if ( val !== undefined ) return val as never
			return "Выберите гос. номер"
		}
		
		/**
		 * ```tree
		 * acts_options *
		 * ```
		 */
		acts_options() {
			return {
			}
		}
		
		/**
		 * ```tree
		 * Act_control $mol_select
		 * 	value?val <=> act?val
		 * 	dictionary <= acts_options
		 * 	hint \Выберите
		 * 	no_options_message \Нет авто на территории
		 * ```
		 */
		@ $mol_mem
		Act_control() {
			const obj = new this.$.$mol_select()
			
			obj.value = (val?: any) => this.act(val)
			obj.dictionary = () => this.acts_options()
			obj.hint = () => "Выберите"
			obj.no_options_message = () => "Нет авто на территории"
			
			return obj
		}
		
		/**
		 * ```tree
		 * Auto_number_field $mol_form_field
		 * 	name \Гос. номер
		 * 	bid <= act_bid
		 * 	Content <= Act_control
		 * ```
		 */
		@ $mol_mem
		Auto_number_field() {
			const obj = new this.$.$mol_form_field()
			
			obj.name = () => "Гос. номер"
			obj.bid = () => this.act_bid()
			obj.Content = () => this.Act_control()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Names $mol_form_group sub /
		 * 	<= Weight_field
		 * 	<= Auto_number_field
		 * ```
		 */
		@ $mol_mem
		Names() {
			const obj = new this.$.$mol_form_group()
			
			obj.sub = () => [
				this.Weight_field(),
				this.Auto_number_field()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * exit_submit?val null
		 * ```
		 */
		@ $mol_mem
		exit_submit(val?: any) {
			if ( val !== undefined ) return val as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Submit $mol_button_major
		 * 	title \Создать запись на выезд
		 * 	click?val <=> exit_submit?val
		 * 	enabled <= submit_allowed
		 * ```
		 */
		@ $mol_mem
		Submit() {
			const obj = new this.$.$mol_button_major()
			
			obj.title = () => "Создать запись на выезд"
			obj.click = (val?: any) => this.exit_submit(val)
			obj.enabled = () => this.submit_allowed()
			
			return obj
		}
		
		/**
		 * ```tree
		 * result?val \
		 * ```
		 */
		@ $mol_mem
		result(val?: any) {
			if ( val !== undefined ) return val as never
			return ""
		}
		
		/**
		 * ```tree
		 * Result $mol_status message <= result?val
		 * ```
		 */
		@ $mol_mem
		Result() {
			const obj = new this.$.$mol_status()
			
			obj.message = () => this.result()
			
			return obj
		}
		
		/**
		 * ```tree
		 * submit_allowed
		 * ```
		 */
		submit_allowed() {
			return this.Form().submit_allowed()
		}
		
		/**
		 * ```tree
		 * Form $mol_form
		 * 	body /
		 * 		<= Centrifuge
		 * 		<= Names
		 * 	submit?val <=> exit_submit?val
		 * 	submit_allowed => submit_allowed
		 * 	buttons /
		 * 		<= Submit
		 * 		<= Result
		 * ```
		 */
		@ $mol_mem
		Form() {
			const obj = new this.$.$mol_form()
			
			obj.body = () => [
				this.Centrifuge(),
				this.Names()
			] as readonly any[]
			obj.submit = (val?: any) => this.exit_submit(val)
			obj.buttons = () => [
				this.Submit(),
				this.Result()
			] as readonly any[]
			
			return obj
		}
	}
	
}

