namespace $ {
	export class $scale_settings_camera extends $mol_row {
		
		/**
		 * ```tree
		 * sub /
		 * 	<= Name
		 * 	<= Url
		 * 	<= Save
		 * 	<= Delete
		 * ```
		 */
		sub() {
			return [
				this.Name(),
				this.Url(),
				this.Save(),
				this.Delete()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * name? \
		 * ```
		 */
		@ $mol_mem
		name(next?: any) {
			if ( next !== undefined ) return next as never
			return ""
		}
		
		/**
		 * ```tree
		 * Name $mol_textarea
		 * 	hint \Наименование
		 * 	value? <=> name?
		 * ```
		 */
		@ $mol_mem
		Name() {
			const obj = new this.$.$mol_textarea()
			
			obj.hint = () => "Наименование"
			obj.value = (next?: any) => this.name(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * rtsp_url? \
		 * ```
		 */
		@ $mol_mem
		rtsp_url(next?: any) {
			if ( next !== undefined ) return next as never
			return ""
		}
		
		/**
		 * ```tree
		 * Url $mol_textarea
		 * 	hint \RTSP URL
		 * 	value? <=> rtsp_url?
		 * ```
		 */
		@ $mol_mem
		Url() {
			const obj = new this.$.$mol_textarea()
			
			obj.hint = () => "RTSP URL"
			obj.value = (next?: any) => this.rtsp_url(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Save_icon $mol_icon_check
		 * ```
		 */
		@ $mol_mem
		Save_icon() {
			const obj = new this.$.$mol_icon_check()
			
			return obj
		}
		
		/**
		 * ```tree
		 * delete? null
		 * ```
		 */
		@ $mol_mem
		delete(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Save $mol_button_minor
		 * 	sub / <= Save_icon
		 * 	hint \Сохранить
		 * 	click? <=> delete?
		 * ```
		 */
		@ $mol_mem
		Save() {
			const obj = new this.$.$mol_button_minor()
			
			obj.sub = () => [
				this.Save_icon()
			] as readonly any[]
			obj.hint = () => "Сохранить"
			obj.click = (next?: any) => this.delete(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Delete_icon $mol_icon_delete
		 * ```
		 */
		@ $mol_mem
		Delete_icon() {
			const obj = new this.$.$mol_icon_delete()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Delete $mol_button_minor
		 * 	sub / <= Delete_icon
		 * 	hint \Удалить камеру
		 * 	click? <=> delete?
		 * ```
		 */
		@ $mol_mem
		Delete() {
			const obj = new this.$.$mol_button_minor()
			
			obj.sub = () => [
				this.Delete_icon()
			] as readonly any[]
			obj.hint = () => "Удалить камеру"
			obj.click = (next?: any) => this.delete(next)
			
			return obj
		}
	}
	
}

