namespace $ {
	export class $scale_dash_gate extends $mol_list {
		
		/**
		 * ```tree
		 * rows / <= Row
		 * ```
		 */
		rows() {
			return [
				this.Row()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * open_submit?val null
		 * ```
		 */
		@ $mol_mem
		open_submit(val?: any) {
			if ( val !== undefined ) return val as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Gate_left_icon $mol_icon_chevron_left_box
		 * ```
		 */
		@ $mol_mem
		Gate_left_icon() {
			const obj = new this.$.$mol_icon_chevron_left_box()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Gate_left $mol_button_major
		 * 	click?val <=> open_submit?val
		 * 	sub / <= Gate_left_icon
		 * ```
		 */
		@ $mol_mem
		Gate_left() {
			const obj = new this.$.$mol_button_major()
			
			obj.click = (val?: any) => this.open_submit(val)
			obj.sub = () => [
				this.Gate_left_icon()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * title \Шлагбаум №1
		 * ```
		 */
		title() {
			return "Шлагбаум №1"
		}
		
		/**
		 * ```tree
		 * Gate_text $mol_paragraph title <= title
		 * ```
		 */
		@ $mol_mem
		Gate_text() {
			const obj = new this.$.$mol_paragraph()
			
			obj.title = () => this.title()
			
			return obj
		}
		
		/**
		 * ```tree
		 * close_submit?val null
		 * ```
		 */
		@ $mol_mem
		close_submit(val?: any) {
			if ( val !== undefined ) return val as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Gate_right_icon $mol_icon_chevron_right_box
		 * ```
		 */
		@ $mol_mem
		Gate_right_icon() {
			const obj = new this.$.$mol_icon_chevron_right_box()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Gate_right $mol_button_major
		 * 	click?val <=> close_submit?val
		 * 	sub / <= Gate_right_icon
		 * ```
		 */
		@ $mol_mem
		Gate_right() {
			const obj = new this.$.$mol_button_major()
			
			obj.click = (val?: any) => this.close_submit(val)
			obj.sub = () => [
				this.Gate_right_icon()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Row $mol_row sub /
		 * 	<= Gate_left
		 * 	<= Gate_text
		 * 	<= Gate_right
		 * ```
		 */
		@ $mol_mem
		Row() {
			const obj = new this.$.$mol_row()
			
			obj.sub = () => [
				this.Gate_left(),
				this.Gate_text(),
				this.Gate_right()
			] as readonly any[]
			
			return obj
		}
	}
	
}

